import {
    LitElement, html, customElement, property
} from 'lit-element';
import { IGridConfig } from './interfaces';
import { gridTemplate } from './templates';


@customElement('free-grid')
export class AppComponent extends LitElement {
    _data: any = [];


    constructor() {
        
        super();
        console.time('grid')
        for (let i = 0; i < 40; i++) {
            this.rows.push(
                {i: i}
            )
        }
        
    }

    @property() config: IGridConfig;
   //  @property() data: any;
    @property() rows: any = [];

    updated() {
       // console.timeEnd('grid')
    }

    set data(value){
        const oldValue = this.data;
        this._data = value;
        if(oldValue.length !== value.length){
           let x = this.getElementsByTagName('free-grid-body')[0]
           if(x){
               x.scrollTop = 0;
               for (let i = 0; i < 40; i++) {
                this.rows[i].i = i;
               }
           }
        }
        this.requestUpdate('data', oldValue);
    }
    get data(){
        return this._data;
    }


    createRenderRoot() {
        return this;
    }

    render() {

        const requestUpdate = (cs: IGridConfig) => {
            this.config = cs;
            this.requestUpdate();
        }

        if (this.config) {
            let updatescrolltop = this.updatescrolltop;
            this.updatescrolltop = null;
            return html`
                ${gridTemplate(this.config, this.data, requestUpdate, this.rows, updatescrolltop)}
            `
        } else {
            console.error('no config set')
        }
        ;
    }
}



interface A {
    a: number
}
interface B extends A {
    filter: boolean
    filterCallback: Function
}
type C = (A & B) | (A | B);

const x = function (x: C) { }

x({ a: 1, filter: true })