import {
    LitElement, html, customElement, property
} from 'lit-element';
import { COL_SETUP as gridConfig, } from './colSetup';
import { DummyDataGenerator } from './dummyDataGenerator';
import { IGridConfig } from '../elements/free-grid/interfaces';


const data = new DummyDataGenerator();

@customElement('app-component')
export class AppComponent extends LitElement {
    private data: any = [];
    private gridConfig: IGridConfig;

    constructor() {
        super();
        this.data = data.generateData(10000);
        this.gridConfig = gridConfig;
    }


    createRenderRoot() {
        return this;
    }

    replaceData(x){
        this.data = data.generateData(x);
        this.requestUpdate();
    }

    addData(x){
        this.data = this.data.concat(data.generateData(x));
        this.requestUpdate();
    }

    render() {
        return html`
        <p>do not use more then 100 rows, rezising just wont work well if you do</p>
        <button @click=${()=>{this.replaceData(100)}}> set 100</button>
        <button @click=${()=>{this.replaceData(50)}}> set 50</button>
        <button @click=${()=>{this.replaceData(10)}}> set 10</button>
        <button @click=${()=>{this.replaceData(1)}}> set 1</button>
        <button @click=${()=>{this.replaceData(0)}}> set 0</button>
        <button @click=${()=>{this.addData(1)}}> add 1</button>
        <button @click=${()=>{this.addData(10)}}> add 10</button>
        <button @click=${()=>{this.addData(100)}}> add 100</button>
        <button @click=${()=>{this.addData(1000)}}> add 1000</button>
        <free-grid 
            style="margin:20px;width:600px;height:400px" 
            class="free-grid" 
            .data=${this.data} 
            .config=${this.gridConfig}>
        </free-grid>
        `;
    }
}
